<template>
  <div class="app-container">
    <table-update :options="tableOptions">
      <template slot-scope="scope">
        <el-button @click="handleShow(scope.data)" type="text" size="small"></el-button>
        <el-button @click="handleEdit(scope.data)" type="text" size="small"></el-button>
      </template>
    </table-update>
  </div>
</template>

<script>
import tableUpdate from "@/components/TableUpdate";

export default {
  data() {
    return {
      tableOptions: {
        // 
        tableRow: [
          {
            key: "date",
            value: "",
            type: "date"
          },
          {
            key: "name",
            value: "",
            type: "input"
          },
          {
            key: "provinces",
            value: "",
            type: "select"
          },
          {
            key: "address",
            value: "",
            type: "textarea"
          }
        ],
        // 
        tableData: [
          {
            date: "2016-05-02",
            name: "",
            provinces: "",
            address: " 1518 "
          },
          {
            date: "2016-05-04",
            name: "",
            provinces: "",
            address: " 1517 "
          },
          {
            date: "2016-05-01",
            name: "",
            provinces: "",
            address: " 1519 "
          },
          {
            date: "2016-05-03",
            name: "",
            provinces: "",
            address: " 1516 "
          }
        ],
        // 
        handleType: null,
        // 
        selectRow: {}
      }
    };
  },
  components: {
    tableUpdate
  },
  created() {},
  methods: {
    formatCity(val) {
      const regex = /.+?(|||||)/g;
      return val.match(regex);
    },
    handleShow(row) {
      this.tableOptions.handleType = "";
      this.tableOptions.selectRow = row;
    },
    handleEdit(row) {
      this.tableOptions.handleType = "";
      row.provinces = this.formatCity(row.provinces);
      console.log(row.provinces)
      this.tableOptions.selectRow = row;
    }
  }
};
</script>
